nav {
  width: 100vw;
  color: #fff;
  text-decoration: none;
  /* border-bottom: 1px solid white; */
  /* box-shadow: 0 5px 15px 0 lightblue; */
  display: flex;
  justify-content: flex-end;
}

nav ul {
  display: flex;
  justify-content: flex-end;
  list-style-type: none;
  /* overflow: hidden; */
  width: 100%;
  /* margin-right: 20px; */
  align-items: right;
}

nav ul li p {
  display: inline;
}

nav ul li {
  margin: 0 20px;
  padding: 0;
}

nav ul li a {
  line-height: 60px;
  margin: 4px;
  font-size: 20px;
  text-decoration: none;
  padding: 0;
  color: white !important;
}

nav ul li svg {
  display: inline;
  margin-right: 8px;
}

.logo-text {
  font-size: 30px;
  margin-left: 30px;
  background-color: red;
  background-image: linear-gradient(45deg, orange, yellow);
  background-size: 100%;
  background-repeat: repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.title {
  margin-top: 7px;
  display: flex;
  flex-direction: column;
  width: 400px;
}

.logo-art {
  width: 195px;
  height: 3px;
  background-color: rgba(160, 241, 237, 0.957);
  position: absolute;
  left: 300px;
  top: 45px;
  z-index: 200;
  animation: slidein 2s forwards;
}

@keyframes slidein {
  0% {
    transform: translateX(50px);
    transform: translateY(-50px);
  }

  50% {
    transform: skewX(-25deg) rotate(-2deg);
  }

  100% {
    transform: translateX(0px);
    transform: translateY(0px);
    transform: skewX(-45deg) rotate(-1deg);
  }
}

.checkbtn {
  cursor: pointer;
  display: none;
}

#checkbx {
  display: none;
}

.nav-link {
  display: inline-block;
  color: white;
  transition: 0.5s;
}

@media screen and (max-width: 375px) {
  .logo-text {
    width: 40%;
    margin-top: 30px;
    margin-left: 3px;
    font-size: 20px;
    line-height: 22px;
  }

  .logo-art {
    top: 54px;
    left: 15px;
    width: 120px;
  }

  nav ul {
    width: 20%;
  }

  nav ul li {
    margin: 3px;
  }

  nav ul li svg {
    font-size: 30px;
    color: white;
    margin: 0;
    padding: 0;
  }

  nav ul li p {
    display: none;
  }
}

@media only screen and (min-width: 376px) and (max-width: 640px) {
  nav a {
    width: 100%;
  }

  nav ul {
    width: 50%;
  }

  .logo-text {
    margin-top: 15px;
    margin-left: 5px;
    font-size: 22px;
    line-height: 20px;
  }

  .logo-art {
    top: 48px;
    left: 15px;
    width: 140px;
  }

  nav ul li {
    margin: 3px;
  }

  nav ul li svg {
    font-size: 30px;
    color: white;
    margin: 0;
    padding: 0;
  }

  nav ul li p {
    display: none;
  }
}

@media screen and (min-width: 641px) and (max-width: 768px) {
  .logo-art {
    top: 55px;
    left: 45px;
    width: 200px;
  }

  nav ul li p {
    display: none;
  }

  nav ul li a {
    font-size: 15px;
    color: white;
    margin: 1px;
    padding: 1px;
  }

  nav ul li svg {
    font-size: 30px;
  }

  nav ul {
    width: 300px;
  }
}

@media screen and (min-width: 769px) and (max-width: 1024px) {
  .checkbtn {
    display: block;
  }

  .logo-text {
    margin-left: 20px;
    padding: 10px 3px;
    /* width: 600px; */
  }

  .logo-art {
    top: 65px;
    left: 35px;
    width: 190px;
  }

  nav ul li p {
    display: none;
  }

  nav ul li a {
    font-size: 15px;
    color: white;
    margin: 4px;
    padding: 4px;
  }

  nav ul {
    width: 300px;
  }
  #checkbx:checked ~ ul {
    left: 0px;
  }
}
